import { REQUEST_SUCCESS, REQUEST_ERROR} from '../actions/FlagActions'

/**
 * Funcin annima encargada de manejar los estados de la aplicacin
 * para el caso de contenedor de semforo
 * @param {Object} state Se define por defecto null para cuando se realice la
                         peticin se cambie por otro
 * @param {Object} action Se guarda la informacin manejada en el reducer
 * @return {String} Si se realiza la peticin, se devuelve la informacin del
                    semforo en json string, en caso contrario null.
 */
export default function (state = null, action) {
    switch (action.type) {
        case REQUEST_SUCCESS:
            return action.body;
        case REQUEST_ERROR:
            return action.body;
            break;
    }
    return state;
}



